                            <v-text-field
                              v-model="editedItem.EstimasiPergi"
                              append-icon="mdi-calendar"
                              color="dark"
                              readonly
                              dense
                              v-bind="attrs"
                              v-on="on"
                            >
                              <template v-slot:prepend v-if="mobile == false">
