                          @input="MenuTglActualPulang = false"
                          year-icon="mdi-calendar-blank"
                          color="red darken-4"
                          class="rounded-lg"
                          locale="id"
                        ></v-date-picker>
                      </v-dialog>
                      </v-col>
                    </v-row>

                    <!-- <v-row no-gutters>
                      <v-col cols="12" sm="4" md="4" lg="4">
                        <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Revisi Ke</span>
                      </v-col>
                      <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">  
                        <v-text-field
                          dense
                          color="dark"
                          v-model="editedItem.RevisiKe"   
                        >
                          <template v-slot:prepend v-if="mobile == false">
