      return this.editedIndex === -1 ? 'Pilih NRK Karyawan' : 'Ubah NRK Karyawan'
    },
    Status () {
      return this.editedIndex === -1 ? 'Baru' : 'Ubah'
    },
    StatusPilih () {
      return this.editedIndex === -1 ? 'Pilih' : 'Pilih'
    },
  },
  
