          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      }else{
      this.Kode = Kode.toUpperCase()
      let a = this.DataPenugasan.filter( function(item){return (item.Kode == Kode.toUpperCase());} );
