              // console.log(res);
              if (res) {
                this.getdata()
              // this.AlertDataTerhapus = true
              }
              
            })
            .catch((err) => {
              console.log(err);
            });
        }
      } else if (args.target.classList.contains("Edit")){
        this.editedItem = args.rowData
        this.editedIndex = 1
        this.DialogTambahPenugasan = true
        // this.KodeCabang = args.rowData.KodeCabang
