    KeluarDialogNrk(){
      this.DialogNrk = false
    },

    AmbilCapital(str) { 
      let newStr = "";
      for (let i = 0; i < str.length; i++) {
          if (str[i].match(/[A-Z]/)) {
              newStr += str[i];
          }
      }
      return newStr;
    },

    // beforeExport: function(args) {
      
    // },

    getdata(){
      if (this.valid == true) {
        api.get('/penugasankaryawan?token='+this.token,{
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.loading = false
            this.DataPenugasan = this.$decrypt(data)
            this.getbawahan()
          })
        },
				err => {
					console.log(err);
				}
			) 
      }
      
    },
    getbawahan(){
      api.get('/GetBawahan/'+this.$user.jabatan.id+'?token='+this.token).then(
        res =>{
          // console.log(res)
          let data = []
          for (let i = 0; i < res.data.length; i++) {
            const ilo = res.data[i];
            for (let e = 0; e < ilo.karyawan.length; e++) {
              const ele = ilo.karyawan[e];
              ele.jabatan = ilo.jabatan
              ele.jabatan.NamaJabatan = ilo.NamaJabatanBawahan
              data.push(ele)
            }
          }
          this.karyawan = data
          // console.log(data)
          this.getjabatan()
          // this.getkaryawan()
        }
      )
    },
    getjabatan(){
      api.get('/jabatan?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
				res => {
					// console.log(res);
          res.data = this.$decrypt(res.data)
          this.jabatan = res.data
				},
				err => {
					console.log(err);
				}
			)
    },

    getkaryawan(){
     if (this.valid == true) {
        api.get('/karyawan?token='+this.token,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
				res => {
					// console.log(res.data);
          console.log(this.$user)
          let user = this.$user
          let data
          if (this.$user.jabatan.NamaJabatan.includes("Head of") ) {
            data = res.data.filter(x => x.jabatan != null ? x.jabatan.Divisi == this.AmbilCapital(user.jabatan.NamaJabatan.substr(8)) : x == this.$user)
          }
          else{
            data = res.data.filter(x => x.jabatan != null ? x.jabatan.Departemen == this.$user.jabatan.Departemen : x == this.$user)
          }
          this.karyawan = data
          // this.loading = false
          this.getjabatan()
				},
				err => {
					console.log(err);
				}
			)
      }
    },

    FormatTanggal(x){
      var todayTime = new Date(x);
          var bulan = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"];
          var month = bulan[todayTime .getMonth()];
          var day = todayTime .getDate();
          var year = todayTime .getFullYear();
          return day + " " + month + " " + year;  
    },

    Reset() {
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
      })
    },
    logout(){
        api.post('/logout', this.token)
        .then(res=>{
          // console.log(res)
          if (res) {
            //do nothing
          }
        })
        localStorage.removeItem('token')
        localStorage.removeItem('user')
        location.reload(false)
      },

    Toast(data){
      this.$swal.fire({
      toast: true,
      icon: 'success',
      title: data,
      animation: false,
      position: 'top-right',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true})
    },  

    AlertItem(){
      window.setInterval(() => {
        this.Alert = false;
        this.AlertDataTerhapus = false;
        this.AlertBerhasilTerubah = false;
      }, 3000)
    },
  },
}
